import React from 'react';
import { RichText } from 'prismic-reactjs';

function showContactHideOthers(updateAboutActive, updateContactActive, updateThumbsActive) {
	updateContactActive(true);
	setTimeout(() => {
		updateAboutActive(false);
		updateThumbsActive(false);
	}, 300)
}

function About({ 
	data,
	updateAboutActive,
	updateContactActive,
	updateThumbsActive
}) {
	const aboutContent = data.find(d => d.type === "about");
	
  return (
    <div className="about-container">
			<div className="about-inner">
				<div>
					<span className="muted">About</span>
				</div>
				<div>
					<RichText render={aboutContent.data.text} />
					<p>Helsinki, Finland
						<br />
						<span
							className="link underline"
							onClick={() => showContactHideOthers(updateAboutActive, updateContactActive, updateThumbsActive)}
						>Get in touch</span>
					</p>
				</div>
			</div>
		</div>
  )
}

export default About;