import React, { useState, useRef, useEffect } from 'react';

function resetWindow() {
  window.scrollTo(0, 0);
  document.body.scrollTop = 0;
}

function Contact() {
  const [emailStatus, updateEmailStatus] = useState("");
  const nameElement = useRef(null);
  const emailElement = useRef(null);
  const messageElement = useRef(null);

  useEffect(() => {
    nameElement.current.onblur = () => resetWindow();
    emailElement.current.onblur = () => resetWindow();
    messageElement.current.onblur = () => resetWindow();
  });

  return (
    <div className="contact-container">
			<div className="contact-inner">
        <div>
					<span className="muted">Contact</span>
				</div>
				<div>
          <p>Want to collaborate or just say hi? Get in touch through the form below.</p>
          <form
            onSubmit={(e) => {
              e.preventDefault();
              const form = e.target;
              const data = new FormData(form);
              const xhr = new XMLHttpRequest();
              xhr.open(form.method, form.action);
              xhr.setRequestHeader("Accept", "application/json");
              xhr.onreadystatechange = () => {
                if (xhr.readyState !== XMLHttpRequest.DONE) return;
                if (xhr.status === 200) {
                  form.reset();
                  updateEmailStatus("SUCCESS");
                } else {
                  updateEmailStatus("ERROR");
                }
              };
              xhr.send(data);
            }}
            action="https://formspree.io/xjvavbpp"
            method="POST"
          >
            <input
              type="text" 
              name="name" 
              placeholder="Name" 
              required
              autoComplete="off"
              ref={nameElement}
            />
            <input 
              type="email" 
              name="email" 
              placeholder="Email" 
              required
              autoComplete="off"
              ref={emailElement}
            />
            <textarea 
              name="message" 
              placeholder="Message" 
              required
              autoComplete="off"
              ref={messageElement}
            />
            {emailStatus === "SUCCESS"
              ? <p>Message sent</p>
              : <button>Send</button>
            }
            {emailStatus === "ERROR" && <p>Ooops! There was an error.</p>}
          </form>
				</div>
      </div>
    </div>
  )
}

export default Contact;